<template><section><h1>支付组件</h1>
<hr>
<h3 id="ji-chu-yong-fa">基础用法</h3>
 <div class="demo-block">
    <so-pay ref="pays" :show="showPayModal" @close="close" @forget="forget" @input-end="inputEnd"></so-pay>
  </div>
<demo-block class="demo-box">
                        <div slot="source" class="source"><html><head></head><body><div>
        <so-pay ref="pays" :show="showPayModal" @close="close" @forget="forget" @input-end="inputEnd">
        </so-pay>
      </div>
      
</body></html></div>
                        
                        <div slot="highlight" class="highlight"><pre v-pre><code v-pre class="language-html">      <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">so-pay</span> 
          <span class="hljs-attr">ref</span>=<span class="hljs-string">"pays"</span>
          <span class="hljs-attr">:show</span>=<span class="hljs-string">"showPayModal"</span>           
          <span class="hljs-attr">@close</span>=<span class="hljs-string">"close"</span>
          <span class="hljs-attr">@forget</span>=<span class="hljs-string">"forget"</span>
          <span class="hljs-attr">@input-end</span>=<span class="hljs-string">"inputEnd"</span>
          &gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">so-pay</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
          data() {
            <span class="hljs-keyword">return</span> {
              <span class="hljs-attr">showPayModal</span>: <span class="hljs-literal">true</span>,
            }
          },
          <span class="hljs-attr">methods</span>:{
            close(){
              <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'关闭密码'</span>)
            },
            forget(){
              <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'忘记密码'</span>)
            },
            inputEnd(val){
              <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'输入的密码是:'</span>+val)
            }
          }
        }
      </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="jie-shou-can-shu">接受参数</h3>
<table class="table"><thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">show</td>
<td style="text-align:center">密码框的显示隐藏</td>
<td style="text-align:center">Boolean</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">digit</td>
<td style="text-align:center">密码的位数</td>
<td style="text-align:center">Number</td>
<td style="text-align:center">6</td>
</tr>
<tr>
<td style="text-align:center">ref</td>
<td style="text-align:center">获取当前插件实例</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">title</td>
<td style="text-align:center">弹窗标题</td>
<td style="text-align:center">String</td>
<td style="text-align:center">请输入支付密码</td>
</tr>
<tr>
<td style="text-align:center">loading-text</td>
<td style="text-align:center">正在支付的文字提示</td>
<td style="text-align:center">String</td>
<td style="text-align:center">正在支付</td>
</tr>
<tr>
<td style="text-align:center">finished-text</td>
<td style="text-align:center">支付成功的文字提示</td>
<td style="text-align:center">String</td>
<td style="text-align:center">支付成功</td>
</tr>
<tr>
<td style="text-align:center">duration</td>
<td style="text-align:center">支付成功提示的显示时间</td>
<td style="text-align:center">Number</td>
<td style="text-align:center">500</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian">事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:center">事件名</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">input-end</td>
<td style="text-align:center">密码输入完成的回调</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">close</td>
<td style="text-align:center">密码弹窗关闭后的回调</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">forget</td>
<td style="text-align:center">点击忘记密码的回调</td>
<td style="text-align:center">-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
        export default {
          data() {
            return {
              showPayModal: true,
            }
          },
          methods:{
            close(){
              console.log('关闭密码')
            },
            forget(){
              console.log('忘记密码')
            },
            inputEnd(val){
              console.log('输入的密码是:'+val)
            }
          }
        }
      </script>


