<template><section><h1>二维码组件</h1>
<hr>
<h3 id="ji-chu-yong-fa">基础用法</h3>
 <div class="demo-block">
     <div class="wrp_code"><so-qrcode :qrcodeurl="qrcodeurl" :size="180" @changeColor="changeColor" :fgColor="fgColor"></so-qrcode></div>
  </div>
<demo-block class="demo-box">
                        <div slot="source" class="source"><html><head></head><body><div class="wrp_code">
        <so-qrcode :qrcodeurl="qrcodeurl" @imgdata="imgData" :size="282"></so-qrcode>
    </div>
    
</body></html></div>
                        
                        <div slot="highlight" class="highlight"><pre v-pre><code v-pre class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"wrp_code"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">so-qrcode</span> <span class="hljs-attr">:qrcodeurl</span>=<span class="hljs-string">"qrcodeurl"</span> <span class="hljs-attr">@imgData</span>=<span class="hljs-string">"imgData"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"282"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">so-qrcode</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
            data(){
                <span class="hljs-keyword">return</span>{
                    <span class="hljs-attr">qrcodeurl</span>:<span class="hljs-string">'https://www.soyoung.com/'</span>,
                    <span class="hljs-attr">fgColor</span>:<span class="hljs-string">'#2CC7C5'</span>
                }
            },
            <span class="hljs-attr">methods</span>:{
                imgData(){
                    <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'绘制'</span>)
                },
                changeColor(){
                    <span class="hljs-keyword">this</span>.fgColor =   <span class="hljs-string">"#"</span>+ (<span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">0x1000000</span> | <span class="hljs-number">0</span>).toString(<span class="hljs-number">16</span>)
                }
            }
        }
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4 id="jie-shou-can-shu">接受参数</h4>
<table class="table"><thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">qrcodeurl</td>
<td style="text-align:center">二维码显示地址</td>
<td style="text-align:center">String</td>
<td style="text-align:center">https://www.soyoung.com</td>
</tr>
<tr>
<td style="text-align:center">size</td>
<td style="text-align:center">二维码尺寸</td>
<td style="text-align:center">Number,String</td>
<td style="text-align:center">100</td>
</tr>
<tr>
<td style="text-align:center">level</td>
<td style="text-align:center">尺寸类型</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">bgColor</td>
<td style="text-align:center">背景颜色</td>
<td style="text-align:center">String</td>
<td style="text-align:center">#ffffff</td>
</tr>
<tr>
<td style="text-align:center">fgColor</td>
<td style="text-align:center">二维码绘制颜色</td>
<td style="text-align:center">String</td>
<td style="text-align:center">#000000</td>
</tr>
</tbody>
</table>
</section></template>

<script>
        export default{
            data(){
                return{
                    qrcodeurl:'https://www.soyoung.com/',
                    fgColor:'#2CC7C5'
                }
            },
            methods:{
                imgData(){
                    console.log('绘制')
                },
                changeColor(){
                    this.fgColor =   "#"+ (Math.random() * 0x1000000 | 0).toString(16)
                }
            }
        }
    </script>


