<template><section><h1>弹窗组件</h1>
<hr>
<h3 id="ji-chu-yong-fa">基础用法</h3>
 <div class="demo-block">
      <so-button @click="modal1Click">modal1</so-button>
      <so-button @click="modal2Click">modal2</so-button>
      <so-button @click="modal3Click">modal3</so-button>
       <so-button @click="modal4Click">modal4</so-button>
      <so-modal :modalShow="modalShow" :type="modalType" :placeHolder="promptText" :promptKind="promptKind" @modalClose="modalClose">
        <div style="width:600px;height:400px;text-align:center;background:#fff;" v-if="modalType === 'modal1'">
          modal1
        </div>
        <div v-else-if="modalType === 'modal3'">
          modal3
        </div>
          <div v-else-if="modalType === 'modal4'">
          modal4
        </div>
      </so-modal>
  </div>
<demo-block class="demo-box">
                        <div slot="source" class="source"><html><head></head><body><div class="wrp_modal">
        <so-modal :modalshow="modalShow" :type="modalType" @modalclose="modalClose" :placeholder="promptText" :promptkind="promptKind">
        <div v-if="modalType === 'modal1'">
          modal1
        </div>
        <div v-else-if="modalType === 'modal3'">
          modal3
        </div>
        <div style="width:200px;height:100px" v-else-if="modalType === 'modal4'">
          modal4
        </div>
      </so-modal>
    </div>
    
</body></html></div>
                        
