                <span class="hljs-keyword">return</span>{
                    <span class="hljs-attr">modalShow</span>: <span class="hljs-literal">false</span>,
                    <span class="hljs-attr">modalType</span>: <span class="hljs-string">'modal2'</span>,
                    <span class="hljs-attr">promptText</span>: <span class="hljs-string">'操作成功'</span>,
                    <span class="hljs-attr">promptKind</span>: <span class="hljs-string">'success'</span>,
                }
            },
            <span class="hljs-attr">methods</span>:{
                <span class="hljs-attr">modal1Click</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
                    <span class="hljs-keyword">this</span>.modalShow = <span class="hljs-literal">true</span>;
                    <span class="hljs-keyword">this</span>.modalType = <span class="hljs-string">'modal1'</span>;
