</div></demo-block>
<h3 id="jie-shou-can-shu">接受参数</h3>
<table class="table"><thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">modalType</td>
<td style="text-align:center">弹窗类型</td>
<td style="text-align:center">String</td>
<td style="text-align:center">modal1</td>
</tr>
<tr>
<td style="text-align:center">modalShow</td>
<td style="text-align:center">弹窗显示</td>
<td style="text-align:center">Boolean</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">placeHolder</td>
<td style="text-align:center">modal2提示文案</td>
<td style="text-align:center">String</td>
<td style="text-align:center">操作成功</td>
</tr>
<tr>
<td style="text-align:center">placeHolder</td>
<td style="text-align:center">modal2提示icon</td>
<td style="text-align:center">String</td>
<td style="text-align:center">success</td>
</tr>
<tr>
<td style="text-align:center">hideTime</td>
<td style="text-align:center">modal2显示的持续时间</td>
<td style="text-align:center">String</td>
<td style="text-align:center">1500</td>
</tr>
<tr>
<td style="text-align:center">position</td>
<td style="text-align:center">弹窗位置</td>
<td style="text-align:center">Object</td>
<td style="text-align:center">{left:0,top:0}</td>
</tr>
</tbody>
</table>
<h3 id="dan-chuang-lei-xing-shuo-ming">弹窗类型说明</h3>
<table class="table"><thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">modal1</td>
<td style="text-align:center">完全自定义对话框</td>
</tr>
<tr>
<td style="text-align:center">modal2</td>
<td style="text-align:center">成功，失败，警告提示</td>
</tr>
<tr>
<td style="text-align:center">modal3</td>
<td style="text-align:center">右侧拉显示弹窗</td>
</tr>
<tr>
<td style="text-align:center">modal4</td>
<td style="text-align:center">底部弹窗</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian-lei-xing">事件类型</h3>
<table class="table"><thead>
<tr>
<th style="text-align:center">事件</th>
<th style="text-align:center">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">modalClose</td>
<td style="text-align:center">弹窗关闭回调</td>
</tr>
</tbody>
</table>
</section></template>

<script>
        export default{
            data(){
                return{
                    modalShow: false,
                    modalType: 'modal2',
                    promptText: '操作成功',
                    promptKind: 'success',
                }
            },
            methods:{
                modal1Click: function() {
                    this.modalShow = true;
                    this.modalType = 'modal1';
                },
                modal2Click: function() {
                    this.modalShow = true;
                    this.modalType = 'modal2';
                },
                modal3Click: function() {
                    this.modalShow = true;
                    this.modalType = 'modal3';
                },
                 modal4Click: function() {
                    this.modalShow = true;
                    this.modalType = 'modal4';
                },
                modalClose: function() {
                    console.log('关闭弹窗')
                    this.modalShow = false;
                },
            }
        }
    </script>


